@import "../../utils/global_styles/variables";
@import "../../utils/global_styles/mixins";


.topWrapper {
  width: 200px;
  text-align: left;
  margin: 0 auto;

  h3 {
    color: $foxyGrey;
    letter-spacing: 1px;
    margin: 20px auto 3px;
    @include text-shadow(2px, 3px);
  }

  .subHeading {
    display: block;
    margin:10px auto 5px;
  }

  .subDetails {
    display: block;
  }
}