ul {
  list-style-type: none; }

@font-face {
  font-family: "PT_Sans-Web-Regular";
  src: local("PT_Sans-Web-Regular");
  url: "../assets/fonts/PT_Sans-Web-Regular.ttf" format("truetype");
  font-weight: normal;
  font-style: normal; }

@font-face {
  font-family: "Georgia";
  src: local("Georgia");
  url: "../assets/fonts/Georgia.ttf" format("truetype");
  font-weight: normal;
  font-style: normal; }

body {
  font-family: "Georgia", sans-serif;
  background: url(https://subtlepatterns.com/patterns/light_noise_diagonal.png);
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  user-select: none;
  transition: all 0.3s ease-in-out; }

/*************TOP BORDER BODY****************/
body:before {
  content: "";
  position: fixed;
  top: -10px;
  left: 0;
  width: 100%;
  height: 10px;
  z-index: 100;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.8); }

/*****************END TOP BORDER BODY************/
#root {
  max-width: 1280px;
  background-color: white;
  margin: 0 auto;
  padding: 20px; }
  @media screen and (max-width: 720px) {
    #root {
      padding: 5px; } }
